import throttle from 'lodash.throttle';

const emailInput = document.querySelector('input[name="email"]');
const messageInput = document.querySelector('textarea[name="message"]');

//function to store data from fields to local storage
const saveToLocalStorage = throttle(() => {
  const feedbackData = {
    email: emailInput.value,
    message: messageInput.value
  };
  localStorage.setItem('feedback-form-state', JSON.stringify(feedbackData));
}, 500); 


emailInput.addEventListener('input', saveToLocalStorage);
messageInput.addEventListener('input', saveToLocalStorage);


// Function to load form field values from local storage
const loadFromLocalStorage = () => {
  const savedData = JSON.parse(localStorage.getItem('feedback-form-state'));
  console.log(savedData);

  if (savedData) {
    document.querySelector('input[name="email"]').value = savedData.email;
    document.querySelector('textarea[name="message"]').value = savedData.message;
  }
};

loadFromLocalStorage();

// Function to clear local storage and form fields on form submission
const handleSubmit = (event) => {
  event.preventDefault();
  const savedData = JSON.stringify(localStorage.getItem('feedback-form-state'));
  console.log('Submitted Feedback:', savedData);
  localStorage.removeItem('feedback-form-state');
  document.querySelector('input[name="email"]').value = '';
  document.querySelector('textarea[name="message"]').value = '';

  console.log(savedData);
};


// // Event listener for form submission
document.querySelector('.feedback-form').addEventListener('submit', handleSubmit);

